@charset "UTF-8";
* {
  box-sizing: border-box; }

body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: #3c425f; }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

html,
body {
  min-width: 1440px;
  min-height: 980px; }

body {
  /* Временно для сетки */
  background-image: url("https://assets.htmlacademy.ru/content/skills/6/case-01/inde.jpg");
  background-size: 1440px auto;
  background-repeat: no-repeat;
  background-position: top center; }

.container {
  width: 1440px;
  margin: 0 auto; }

.page {
  display: grid;
  grid-template-areas: "navigation header" "navigation main" "footer footer";
  grid-template-columns: 280px 1fr;
  grid-template-rows: 80px 1fr 60px;
  min-height: 100vh; }

.main {
  grid-area: main;
  padding: 30px;
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: 300px auto 1fr;
  grid-template-areas: "slider contacts" "offers contacts" "video contacts";
  grid-gap: 20px;
  background-color: #f1f3fe; }

.item__letter::before {
  content: attr(data-text);
  width: 40px;
  height: 40px;
  font-weight: 900;
  font-size: 22px;
  line-height: 40px;
  text-align: center;
  color: #b4bef0;
  border-radius: 50%;
  background-color: #f1f3fe; }

.nav {
  grid-area: navigation;
  display: grid;
  grid-template-rows: 80px 1fr auto;
  background-color: #ffffff;
  border-right: 1px solid #b4bef0; }

.nav__logo {
  margin: 24px 30px 15px; }

.nav__list {
  padding: 0;
  margin: 0;
  list-style: none; }

.nav__list--tools {
  margin-bottom: 10px; }

.nav__item--active {
  background-color: #f1f3fe; }

.nav__link {
  position: relative;
  display: block;
  padding: 20px 25px 20px 63px;
  font-size: 16px;
  line-height: 20px;
  color: #3c425f;
  text-decoration: none; }
  .nav__link::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    left: 30px; }

.nav__link--cab::before {
  background-image: url("../img/cabinet.svg");
  background-repeat: no-repeat;
  background-position: center; }

.nav__link--class::before {
  background-image: url("../img/class.svg");
  background-repeat: no-repeat;
  background-position: center; }

.nav__link--book::before {
  background-image: url("../img/book.svg");
  background-repeat: no-repeat;
  background-position: center; }

.nav__link--home::before {
  background-image: url("../img/home.svg");
  background-repeat: no-repeat;
  background-position: center; }

.nav__link--exam::before {
  background-image: url("../img/exam.svg");
  background-repeat: no-repeat;
  background-position: center; }

.nav__link--tools::before {
  background-image: url("../img/tools.svg");
  background-repeat: no-repeat;
  background-position: center; }

.nav__link--active {
  font-weight: 500;
  color: #576bcc; }

.head {
  grid-area: header;
  display: grid;
  grid-template-columns: 1fr auto 80px;
  background-color: #ffffff;
  border-bottom: 1px solid #b4bef0; }

.head__profile {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  max-width: 400px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 20px; }

.head__search {
  margin: 10px 0 0 30px; }

.head__input {
  padding: 20px 20px 20px 32px;
  font-family: inherit;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  color: #3c425f;
  border: none;
  background-image: url("../img/search.svg");
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 20px 20px; }
  .head__input::placeholder {
    color: #b4bef0; }

.head__profile {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  max-width: 400px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 20px; }

.head__link {
  font-size: 16px;
  line-height: 40px;
  color: #3c425f;
  text-decoration: none; }

.head__btn--profile {
  position: relative;
  width: 30px;
  border: none;
  background-color: transparent; }
  .head__btn--profile::before {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border: 6px solid #576bcc;
    border-left-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent; }

.head__btn--notifications {
  width: 25px;
  height: 25px;
  margin: 27px;
  background-image: url("../img/notifications.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px 20px;
  background-color: transparent;
  border: none; }
  .head__btn--notifications span {
    display: inline-block;
    position: relative;
    right: -5px;
    top: -5px;
    width: 18px;
    height: 18px;
    font-weight: 900;
    font-size: 10px;
    line-height: 14px;
    color: #ffffff;
    background-color: #ff8f0b;
    border-radius: 50%;
    border: 2px solid #ffffff; }

.slider {
  position: relative;
  grid-area: slider;
  width: 820px;
  height: 300px;
  padding: 32px 60px;
  overflow: hidden;
  box-sizing: border-box;
  font-weight: 300;
  font-size: 18px;
  line-height: 28px; }

.slider__img {
  position: absolute;
  left: 0;
  top: 0; }

.slider__title {
  position: relative;
  margin-bottom: 10px;
  font-weight: 900;
  font-size: 28px;
  line-height: 48px;
  color: #ffffff; }

.slider__paragraph {
  position: relative;
  margin: 0 0 30px 0;
  max-width: 470px;
  color: #ffffff; }

.slider__link {
  position: relative;
  padding: 10px 20px;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  text-decoration: none;
  background-color: #ff8f0b;
  border-radius: 4px; }

.slider__btn {
  position: absolute;
  width: 64px;
  height: 64px;
  left: -32px;
  top: 0;
  bottom: 0;
  margin: auto;
  border-radius: 50%;
  border: none;
  background-color: #ffffff;
  background-image: url("../img/arrow.svg");
  background-repeat: no-repeat;
  background-position: left 35px center;
  background-size: 18px 18px; }

.slider__btn--next {
  left: auto;
  right: -32px;
  top: 0;
  bottom: 0;
  margin: auto;
  transform: rotate(180deg); }

.offers {
  grid-area: offers; }

.offers__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px; }

.offers__item {
  display: grid;
  /* для выравнивания ссылки */
  grid-template-rows: auto 1fr auto;
  padding: 20px 30px;
  background-color: #ffffff;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);
  border-radius: 4px; }

.offers__title {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #3c425f; }

.offers__text {
  margin: 5px 0 5px 0;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: #3c425f; }

.offers__text--time {
  margin: 0 0 8px 0;
  font-weight: 900;
  font-size: 14px;
  line-height: 20px;
  color: #0bc4cf; }

.offers__footer {
  text-align: right; }

.offers__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #576bcc;
  text-decoration: none; }

.video {
  grid-area: video;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 20px; }

.video__lesson {
  width: 540px;
  height: 300px; }

.video__title {
  margin: 20px 0 13px 30px;
  border-bottom: 1px solid #e4e8fc;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px; }

.video__wrap {
  position: relative;
  height: 300px;
  background: #ffffff;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);
  border-radius: 4px; }

.video__list {
  padding: 0;
  margin: 0;
  list-style: none;
  height: 235px;
  overflow-y: auto; }

.video__item {
  display: grid;
  grid-template-columns: 32px auto;
  grid-template-rows: auto auto;
  grid-template-areas: "button title" "button desc";
  grid-column-gap: 13px;
  padding: 8px 10px 8px 30px;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  color: #3c425f; }

.video__item--active {
  background-color: #f1f3fe; }

.video__btn {
  position: relative;
  grid-area: button;
  width: 32px;
  height: 32px;
  background-color: #f1f3fe;
  border-radius: 4px;
  border: none; }
  .video__btn::before {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    left: 0;
    top: 0;
    transform: translate(12px, 10px);
    border-top: 6px solid transparent;
    border-left: 12px solid #576bcc;
    border-bottom: 6px solid transparent;
    border-right: 12px solid transparent; }

.video__btn--active {
  background-color: #576bcc; }
  .video__btn--active::before {
    border-left-color: #ffffff; }

.video__text {
  margin: 0; }

.video__text--title {
  grid-area: title;
  font-weight: 900; }

.video__text--para {
  grid-area: desc; }
